namespace $ {
	export class $bun_scroll_hor extends $mol_view {
		
		/**
		 * ```tree
		 * event *
		 * 	^
		 * 	wheel? <=> on_wheel?
		 * ```
		 */
		event() {
			return {
				...super.event(),
				wheel: (next?: any) => this.on_wheel(next)
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * on_wheel? null
		 * ```
		 */
		@ $mol_mem
		on_wheel(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
	}
	
}

