namespace $ {
	export class $bun_alh_controls_page extends $bun_alh_page {
		
		/**
		 * ```tree
		 * app_page? \
		 * ```
		 */
		@ $mol_mem
		app_page(next?: any) {
			if ( next !== undefined ) return next as never
			return ""
		}
		
		/**
		 * ```tree
		 * title \Управление
		 * ```
		 */
		title() {
			return "Управление"
		}
		
		/**
		 * ```tree
		 * with_back true
		 * ```
		 */
		with_back() {
			return true
		}
		
		/**
		 * ```tree
		 * back? <=> open_start_page?
		 * ```
		 */
		back(next?: any) {
			return this.open_start_page(next)
		}
		
		/**
		 * ```tree
		 * back_hint \Назад в меню
		 * ```
		 */
		back_hint() {
			return "Назад в меню"
		}
		
		/**
		 * ```tree
		 * content / <= Page_content
		 * ```
		 */
		content() {
			return [
				this.Page_content()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * open_start_page? null
		 * ```
		 */
		@ $mol_mem
		open_start_page(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Ways_title $mol_view sub / \В bun_alh существует два способа управления элементами:
		 * ```
		 */
		@ $mol_mem
		Ways_title() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				"В bun_alh существует два способа управления элементами:"
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * way_title* \
		 * ```
		 */
		way_title(id: any) {
			return ""
		}
		
		/**
		 * ```tree
		 * Way_title* $mol_view sub / <= way_title*
		 * ```
		 */
		@ $mol_mem_key
		Way_title(id: any) {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.way_title(id)
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * way_description* \
		 * ```
		 */
		way_description(id: any) {
			return ""
		}
		
		/**
		 * ```tree
		 * Way_description* $mol_view sub / <= way_description*
		 * ```
		 */
		@ $mol_mem_key
		Way_description(id: any) {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.way_description(id)
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Way*0 $mol_view
		 * 	dom_name \li
		 * 	sub /
		 * 		<= Way_title*
		 * 		<= Way_description*
		 * ```
		 */
		@ $mol_mem_key
		Way(id: any) {
			const obj = new this.$.$mol_view()
			
			obj.dom_name = () => "li"
			obj.sub = () => [
				this.Way_title(id),
				this.Way_description(id)
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * way_list / <= Way*0
		 * ```
		 */
		way_list() {
			return [
				this.Way("0")
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Way_list $mol_view
		 * 	dom_name \ul
		 * 	sub <= way_list
		 * ```
		 */
		@ $mol_mem
		Way_list() {
			const obj = new this.$.$mol_view()
			
			obj.dom_name = () => "ul"
			obj.sub = () => this.way_list()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Board_content $mol_view sub /
		 * 	<= Ways_title
		 * 	<= Way_list
		 * ```
		 */
		@ $mol_mem
		Board_content() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.Ways_title(),
				this.Way_list()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Board $bun_alh_board
		 * 	title \Управление элементами в bun_alh
		 * 	content / <= Board_content
		 * ```
		 */
		@ $mol_mem
		Board() {
			const obj = new this.$.$bun_alh_board()
			
			obj.title = () => "Управление элементами в bun_alh"
			obj.content = () => [
				this.Board_content()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Page_content $mol_view sub / <= Board
		 * ```
		 */
		@ $mol_mem
		Page_content() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.Board()
			] as readonly any[]
			
			return obj
		}
	}
	
}

