namespace $ {
	export class $bun_alh_board extends $mol_view {
		
		/**
		 * ```tree
		 * sub /
		 * 	<= Title
		 * 	<= Content
		 * ```
		 */
		sub() {
			return [
				this.Title(),
				this.Content()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Title $mol_view sub / <= title
		 * ```
		 */
		@ $mol_mem
		Title() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.title()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * content /
		 * ```
		 */
		content() {
			return [
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * Content $mol_view sub <= content
		 * ```
		 */
		@ $mol_mem
		Content() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => this.content()
			
			return obj
		}
	}
	
}

