namespace $ {
	export class $bun_alh_app extends $mol_view {
		
		/**
		 * ```tree
		 * title \bun_alh
		 * ```
		 */
		title() {
			return "bun_alh"
		}
		
		/**
		 * ```tree
		 * attr *
		 * 	^
		 * 	mol_theme \$mol_theme_light
		 * ```
		 */
		attr() {
			return {
				...super.attr(),
				mol_theme: "$mol_theme_light"
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * Start_page $bun_alh_page
		 * 	title \bun_alh
		 * 	content / <= Start_menu
		 * ```
		 */
		@ $mol_mem
		Start_page() {
			const obj = new this.$.$bun_alh_page()
			
			obj.title = () => "bun_alh"
			obj.content = () => [
				this.Start_menu()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * Controls_page $bun_alh_controls_page app_page? <=> page?
		 * ```
		 */
		@ $mol_mem
		Controls_page() {
			const obj = new this.$.$bun_alh_controls_page()
			
			obj.app_page = (next?: any) => this.page(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * element_icon*
		 * ```
		 */
		element_icon(id: any) {
			return this.game().element_icon(id)
		}
		
		/**
		 * ```tree
		 * element_name*
		 * ```
		 */
		element_name(id: any) {
			return this.game().element_name(id)
		}
		
		/**
		 * ```tree
		 * element_ids_known
		 * ```
		 */
		element_ids_known() {
			return this.game().element_ids_known()
		}
		
		/**
		 * ```tree
		 * element_ids_combined
		 * ```
		 */
		element_ids_combined() {
			return this.game().element_ids_combined()
		}
		
		/**
		 * ```tree
		 * game $bun_alh_game
		 * 	element_icon* => element_icon*
		 * 	element_name* => element_name*
		 * 	element_ids_known => element_ids_known
		 * 	element_ids_combined => element_ids_combined
		 * ```
		 */
		@ $mol_mem
		game() {
			const obj = new this.$.$bun_alh_game()
			
			return obj
		}
		
		/**
		 * ```tree
		 * Game_page $bun_alh_game_page
		 * 	model <= game
		 * 	app_page? <=> page?
		 * ```
		 */
		@ $mol_mem
		Game_page() {
			const obj = new this.$.$bun_alh_game_page()
			
			obj.model = () => this.game()
			obj.app_page = (next?: any) => this.page(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Recipes_page $bun_alh_game_recipes_page
		 * 	app_page? <=> page?
		 * 	elements <= element_ids_combined_reversed
		 * 	element_icon* <= element_icon*
		 * 	element_name* <= element_name*
		 * ```
		 */
		@ $mol_mem
		Recipes_page() {
			const obj = new this.$.$bun_alh_game_recipes_page()
			
			obj.app_page = (next?: any) => this.page(next)
			obj.elements = () => this.element_ids_combined_reversed()
			obj.element_icon = (id: any) => this.element_icon(id)
			obj.element_name = (id: any) => this.element_name(id)
			
			return obj
		}
		
		/**
		 * ```tree
		 * play? null
		 * ```
		 */
		@ $mol_mem
		play(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Start_button $bun_alh_app_menu_button
		 * 	title \Играть
		 * 	click? <=> play?
		 * ```
		 */
		@ $mol_mem
		Start_button() {
			const obj = new this.$.$bun_alh_app_menu_button()
			
			obj.title = () => "Играть"
			obj.click = (next?: any) => this.play(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * open_controls_page? null
		 * ```
		 */
		@ $mol_mem
		open_controls_page(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * Controls_button $bun_alh_app_menu_button
		 * 	title \Управление
		 * 	click? <=> open_controls_page?
		 * ```
		 */
		@ $mol_mem
		Controls_button() {
			const obj = new this.$.$bun_alh_app_menu_button()
			
			obj.title = () => "Управление"
			obj.click = (next?: any) => this.open_controls_page(next)
			
			return obj
		}
		
		/**
		 * ```tree
		 * Start_menu $mol_view sub /
		 * 	<= Start_button
		 * 	<= Controls_button
		 * ```
		 */
		@ $mol_mem
		Start_menu() {
			const obj = new this.$.$mol_view()
			
			obj.sub = () => [
				this.Start_button(),
				this.Controls_button()
			] as readonly any[]
			
			return obj
		}
		
		/**
		 * ```tree
		 * page? \
		 * ```
		 */
		@ $mol_mem
		page(next?: any) {
			if ( next !== undefined ) return next as never
			return ""
		}
		
		/**
		 * ```tree
		 * element_ids_combined_reversed /
		 * ```
		 */
		element_ids_combined_reversed() {
			return [
			] as readonly any[]
		}
	}
	
}

